/* hero css start */
.hero {
  padding-top: rem(210px);
  padding-bottom: rem(180px);
  position: relative;
  z-index: 1;
  @include media(991px) {
    padding-top: rem(170px); }
  &::after {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.55;
    z-index: -1; }
  &__subtitle {
    color: $base-color;
    font-size: rem(22px);
    @include media(767px) {
      font-size: rem(18px); } }
  &__title {
    font-size: rem(56px);
    color: #fff;
    @include media(767px) {
      font-size: rem(42px); } }
  &__description {
    color: #fff; } }
.hero-search-form {
  background-color: #fff;
  padding: rem(10px);
  @include border-radius(8px);
  @include d-flex;
  position: relative;
  z-index: 1;
  @include media(575px);
  padding: rem(15px);
  &::before {
    position: absolute;
    content: '';
    top: -10px;
    left: -10px;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: rgba(#fff, 0.2);
    @include border-radius(8px);
    z-index: -1; }
  .input-group {
    width: calc((100% / 2) - (140px / 2));
    align-items: center;
    @include media(575px) {
      width: 100%;
      border: 1px solid $border-color;
      margin-bottom: 20px;
      padding: 0 rem(15px);
      @include border-radius(5px); }
    i {
      font-size: rem(20px); } }
  .form--control {
    border: none;
    border-right: 1px solid $border-color;
    @include border-radius(0);
    @include media(575px) {
      border-right: none;
      padding: rem(10px); }
    &:focus {
      box-shadow: none;
      border-color: $border-color; } }
  .select {
    border: none;
    @include border-radius(0);
    @include media(575px) {
      padding: rem(10px); } }
  .hero-search-form-btn {
    width: 120px;
    margin-left: 20px;
    background-color: $base-color;
    color: #fff;
    padding: rem(10px) rem(20px);
    @include border-radius(5px);
    @extend %transition;
    @include media(575px) {
      width: 100%;
      margin-left: 0; }
    &:hover {
      background-color: darken($base-color, 5%); } } }
/* hero css end */

/* inner hero section css start */
.inner-hero {
  position: relative;
  z-index: 1;
  padding-top: 130px;
  padding-bottom: 70px;
  &::before {
    @include positionTwo;
    background-color: $base-color-two;
    opacity: 0.45;
    z-index: -1; }
  .page-title {
    color: #fff; } }
/* inner hero section css end */
