/* sidebar css start */
.sidebar {
  padding: rem(20px) rem(15px);
  background-color: #fff;
  border: 2px solid lighten(#000, 95%);
  @include border-radius(8px); }
.sidebar-widget {
  & + & {
    margin-top: rem(40px); }
  &__title {
    display: flex;
    flex-wrap: wrap;
    font-size: rem(16px);
    margin-bottom: rem(15px);
    font-weight: 700;
    span {
      width: calc(100% - 30px); }
    .title-btn {
      width: 30px;
      background-color: transparent; } } }
.sidebar-menu {
  li {
    a {
      color: $para-color; } }
  .sidebar-menu-img {
    max-height: 20px;
    margin-right: 5px; }
  .sidebar-dropdown + .sidebar-dropdown {
    margin-top: 10px; }
  .sidebar-dropdown {
    position: relative;
    font-family: $heading-font;
    > a {
      font-weight: 500;
      &:hover {
        color: $base-color; }
      span {
        font-weight: 400;
        font-size: rem(14px);
        color: #777;
        &::before {
          content: '('; }
        &::after {
          content: ')'; } } }
    &::after {
      position: absolute;
      content: '';
      top: 0;
      right: 0; } }
  .sidebar-submenu {
    padding-left: 28px;
    .sidebar-menu-item {
      a {
        color: $base-color;
        &::before {
          content: '- '; }
        span {
          color: #777;
          font-size: rem(14px);
          &::before {
            content: '('; }
          &::after {
            content: ')'; } } } } } }

.ad-details-widget {
  background-color: #fff;
  border: 1px solid $border-color;
  @include border-radius(5px);
  &__title {
    padding: rem(10px) rem(20px);
    background-color: #f1f1f1; }
  &__body {
    padding: 20px; } }

.user-info-list {
  li {
    padding: 15px 0;
    @include d-flex;
    align-items: center;
    border-bottom: 1px dashed $border-color;
    &:first-child {
      padding-top: 0; }
    &:last-child {
      border-bottom: none;
      padding-bottom: 0; }
    .icon {
      width: 45px;
      height: 45px;
      background-color: $base-color;
      @include border-radius(5px);
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        font-size: 26px; } }
    .content {
      width: calc(100% - 45px);
      padding-left: 20px;
      .caption {
        font-size: rem(14px); }
      .value {
        font-size: rem(16px);
        font-weight: 700; } } } }
/* sidebar css end */
