/* category section css start */
.category-wrapper {
  padding: rem(50px);
  background-color: #fff;
  margin-top: rem(-70px);
  position: relative;
  z-index: 2;
  box-shadow: 0 10px 45px rgba(#000, 0.05);
  @include media(575px) {
    padding: rem(50px) rem(30px); }
  @include media(380px) {
    padding: rem(40px) rem(20px); } }
.category-item {
  text-align: center;
  padding: rem(20px);
  border: 1px solid $border-color;
  @include border-radius(3px);
  @extend %transition;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  &:hover {
    background-color: rgba($base-color, 0.05);
    box-shadow: 0 3px 10px rgba(#000, 0.05);
    @include transform(translateY(-5px)); }
  &__thumb {
    img {
      max-height: 55px; } }
  &__content {
    margin-top: rem(8px);
    .title {
      font-weight: 600;
      font-size: rem(16px); } }
  &__count {
    font-size: rem(14px);
    color: #777;
    &::before {
      content: '('; }
    &::after {
      content: ')'; } } }

.page-link-inline-menu {
  @include d-flex;
  li {
    font-size: rem(14px);
    font-weight: 500;
    &::after {
      font-family: 'Line Awesome Free';
      font-weight: 900;
      content: "\f105";
      font-size: rem(12px); }
    &:last-child {
      &::after {
        display: none; } } } }
.ad-search-wrapper {
  margin-bottom: rem(30px);
  .ad-search-btn {
    background-color: $base-color-two;
    color: #fff;
    padding: rem(10px) rem(20px); } }
.ad-search-form {
  position: relative;
  .form--control {
    padding-right: rem(60px); }
  .ad-search-form-btn {
    position: absolute;
    top: 5px;
    right: 5px;
    height: calc(100% - 10px);
    width: 55px;
    background-color: $base-color-two;
    color: #fff;
    @include border-radius(5px);
    font-size: rem(20px); } }

.select-menu-list {
  li {
    padding: rem(10px) 0;
    border-bottom: 1px dashed $border-color;
    &:last-child {
      border-bottom: none; }
    &.active {
      .drop-menu {
        display: block; } }
    a {
      display: block; }
    > a {
      color: $para-color;
      &:hover {
        color: $base-color; } }
    .drop-menu {
      padding-left: rem(18px);
      margin-top: rem(15px);
      display: none;
      li {
        &:first-child {
          border-top: 1px dashed $border-color; } } }
    &.has-drop-menu {
      cursor: pointer;
      position: relative;
      &::after {
        position: absolute;
        font-family: 'Line Awesome Free';
        font-weight: 900;
        content: "\f105";
        top: rem(10px);
        right: 0; } }
    .select-menu-img {
      max-width: 20px; } } }
.filter-open-btn {
  padding: rem(8px) rem(20px);
  @include border-radius(3px);
  background-color: $base-color;
  color: #fff;
  display: none;
  @include media(991px) {
    display: inline-block; } }
.sidebar-close-btn {
  display: none;
  @include media(991px) {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    background-color: $danger;
    color: #fff;
    width: 30px;
    height: 30px; } }

.filter-sidebar {
  @extend %transition;
  @include media(991px) {
    position: fixed;
    top: 70px;
    left: -105%;
    z-index: 9;
    @include border-radius(0 !important);
    height: calc(100vh - 70px);
    overflow: scroll;
    &.active {
      left: 0; } } }
/* category section css end */

/* category details section css start */
.category-details-wrapper {
  margin-top: 35px; }
.main-thumb-slider {
  .main-thumb {
    background-color: #f1f1f1;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    min-height: 520px;
    @include border-radius(5px);
    position: relative;
    @include media(767px) {
      min-height: 360px; } } }
.fullview-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  &::after {
    position: absolute;
    content: "\f31e";
    font-family: 'Line Awesome Free';
    font-weight: 900;
    top: 0;
    right: 0;
    width: 45px;
    height: 45px;
    background-color: rgba(#000, 0.75);
    color: #fff;
    font-size: rem(24px);
    display: inline-flex;
    justify-content: center;
    align-items: center; } }
.ad-details-nav-slider {
  .slick-list {
    margin: 0 -15px; }
  .slick-arrow {
    position: absolute;
    top: 50%;
    width: 35px;
    height: 35px;
    margin-top: -17px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    @include border-radius(50%);
    border: 1px solid $border-color;
    z-index: 1;
    &.prev {
      left: -15px; }
    &.next {
      right: -15px; } }
  .ad-details-nav-thumb {
    margin: 0 15px;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    background-color: #f1f1f1;
    @include border-radius(5px);
    @extend %transition;
    cursor: pointer;
    &.slick-current.slick-active {
      box-shadow: inset 0 0 4px $base-color; }
    img {
      max-height: 80px; } } }
.meta-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: -5px -7px;
  li {
    padding: 5px 7px;
    display: flex;
    align-items: center;
    i {
      font-size: rem(18px);
      margin-right: 5px; }
    a {
      color: $para-color;
      &:hover {
        color: $base-color; } } } }
.ad-shape-menu {
  position: relative;
  &:hover {
    .share-sub-menu {
      opacity: 1;
      visibility: visible;
      top: 100%; } }
  .ad-share-btn {
    background-color: transparent;
    padding: 6px 20px;
    border: 1px solid $border-color;
    @include border-radius(3px);
    &:hover {
      background-color: #f1f1f1; } } }

.share-sub-menu {
  background-color: #fff;
  border: 1px solid $border-color;
  @include border-radius(3px);
  position: absolute;
  width: 150px;
  top: 120%;
  right: 0;
  box-shadow: 0 5px 15px  rgba(#000, 0.1);
  opacity: 0;
  visibility: hidden;
  @extend %transition;
  li {
    &:last-child {
      a {
        border-bottom: none; } }
    a {
      display: block;
      color: $para-color;
      padding: 8px  10px;
      border-bottom: 1px dashed $border-color;
      @extend %transition;
      &:hover {
        color: $base-color;
        background-color: #f1f1f159; } } } }
.ad-save {
  &__btn {
    background-color: transparent;
    padding: 6px 20px;
    border: 1px solid $border-color;
    @include border-radius(3px);
    &:hover {
      background-color: #f1f1f1; } } }
.ad-details-thumb-area {
  position: relative;
  .ad-details-price {
    position: absolute;
    top: 0;
    left: 0;
    -webkit-clip-path: polygon(100% 0%, 80% 50%, 100% 100%, 0 100%, 0% 50%, 0 0);
    clip-path: polygon(100% 0%, 80% 50%, 100% 100%, 0 100%, 0% 50%, 0 0);
    width: 120px;
    background-color: $base-color;
    color: #fff;
    z-index: 1;
    padding: rem(10px) rem(20px);
    font-size: rem(28px);
    font-weight: 700; } }
.ad-details-content {
  margin-top: 45px;
  max-height: 400px;
  overflow: hidden;
  position: relative;
  &::after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background: #fff;
    background: -webkit-linear-gradient(to top, rgba(#fff, 0.8), rgba(#fff, 0.35));
    background: linear-gradient(to top, rgba(#fff, 0.8), rgba(#fff, 0.35)); }
  &.active {
    max-height: 100%;
    &::after {
      display: none; } } }
.ad-details-show-btn {
  background-color: #90909014;
  width: 100%;
  padding: 10px 30px;
  .text-two {
    display: none; }
  &.active {
    .text-one {
      display: none; }
    .text-two {
      display: inline-block; } } }

.ad-details-content-footer {
  @include d-flex;
  justify-content: space-between;
  padding: 15px;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(#000, 0.1);
  @include media(480px) {
    flex-direction: column;
    text-align: center; }
  .right {
    @include media(480px) {
      margin-top: 10px; } } }

.related-ad-slider {
  .slick-list {
    margin: 0 rem(-7px); }
  .single-slide {
    margin: 0 rem(7px); }
  .slick-arrow {
    position: absolute;
    top: -35px;
    &.prev {
      right: 30px; }
    &.next {
      right: 0; } } }
/* category details section css end */

/* feature section css start */
.feature-ad-slider {
  .slick-list {
    margin: 0 -8px; }
  .single-slide {
    margin: 0 8px; }
  .slick-arrow {
    position: absolute;
    top: -75px;
    right: 0;
    width: 35px;
    height: 30px;
    background-color: $base-color;
    color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    @include border-radius(3px);
    &.prev {
      right: 45px; } } }

.ad-item {
  padding: rem(10px);
  @include border-radius(8px);
  @extend .section--bg;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  &:hover {
    .ad-item__wrapper {
      @include transform(translateY(-5px));
      box-shadow: 0 5px 20px rgba(#000, 0.15); } }
  &__thumb {
    width: 100%;
    a {
      display: block;
      text-align: center; }
    img {
      max-height: 285px;
      display: inline-block; } }
  &__meta {
    li {
      display: flex;
      align-items: center;
      i {
        font-size: rem(18px);
        margin-right: 5px; }
      a {
        color: $para-color;
        &:hover {
          color: $base-color; } } } }
  &__wrapper {
    padding: 20px;
    background-color: #fff;
    @include border-radius(5px);
    box-shadow: 0 3px 10px rgba(#000, 0.1);
    width: 100%;
    @extend %transition; }
  &__footer {
    .price {
      font-family: $heading-font;
      font-size: rem(24px);
      color: $base-color;
      font-weight: 600; } }
  &.list--style {
    height: auto;
    .ad-item__thumb {
      width: 30%;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      @include media(767px) {
        width: 100%; }
      img {
        min-height: 100px;
        max-height: 200px; } }
    .ad-item__wrapper {
      width: 70%;
      @include d-flex;
      @include media(767px) {
        width: 100%; }
      .ad-item__content {
        width: calc(100% - 150px);
        @include media(575px) {
          width: 100%; } }
      .ad-item__meta {
        @include d-flex;
        margin: -5px -7px;
        li {
          margin: 5px 7px;
          color: #777;
          a {
            color: #777;
            &:hover {
              color: $base-color; } } } }
      .ad-item__footer {
        width: 150px;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        justify-content: flex-end;
        text-align: right;
        @include media(575px) {
          width: 100%;
          justify-content: flex-start;
          text-align: left;
          margin-top: 15px; }
        .price {
          width: 100%; } } } } }
.ad-item + .ad-item {
  margin-top: 10px; }
/* feature section css end */

/* location section css start */
.location-card {
  overflow: hidden;
  @include border-radius(8px);
  position: relative;
  @extend %transition;
  &:hover {
    box-shadow: 0 5px 15px 1px rgba(#000, 0.15);
    @include transform(translateY(-5px));
    img {
      @include transform(scale(1.15)); }
    .overlay-content {
      background-color: rgba(#000, 0.45); } }
  img {
    width: 100%;
    @extend %transition; }
  .overlay-content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(#000, 0.25);
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @extend %transition;
    .title {
      color: #fff; }
    .location-amount {
      color: #fff;
      &::before {
        content: '('; }
      &::after {
        content: ')'; } } } }
/* location section css end */

/* cta section css start */
.feature-item {
  @media (max-width: 991px) and (min-width: 480px) {
    @include d-flex; }
  &__icon {
    @media (max-width: 991px) and (min-width: 480px) {
      width: 95px; }
    img {
      max-height: 65px; } }
  &__content {
    margin-top: 25px;
    @media (max-width: 991px) and (min-width: 480px) {
      width: calc(100% - 95px);
      margin-top: 0; } } }
.cta-wrapper {
  padding: rem(50px);
  background-color: $base-color;
  overflow: hidden;
  position: relative;
  z-index: 1;
  @include border-radius(8px);
  box-shadow: 0 5px 10px rgba(#000, 0.1);
  @include media(480px) {
    padding: rem(30px); }
  &::before {
    position: absolute;
    content: '';
    top: 50%;
    left: -120px;
    @include transform(translateY(-50%));
    width: 350px;
    height: 350px;
    border: 1px solid #fff;
    background-color: transparent;
    @include border-radius(50%);
    opacity: 0.5;
    z-index: -1; }
  &::after {
    position: absolute;
    content: '';
    top: -40px;
    right: -60px;
    width: 200px;
    height: 200px;
    background: transparent;
    background-image: radial-gradient(#fff 20%, transparent 0),radial-gradient(#fff 20%, transparent 0);
    background-size: 10px 10px;
    background-position: 0 0, 25px 25px;
    @include border-radius(50%);
    z-index: -1; } }
/* cta section css end */

/* account section css start */
.account-wrapper {
  @include d-flex;
  align-items: stretch;
  background-color: #fff;
  .left {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding: rem(30px);
    @include border-radius(8px 0 0 8px);
    overflow: hidden;
    position: relative;
    z-index: 1;
    @include media(991px) {
      width: 100%;
      padding: rem(50px) rem(30px);
      @include border-radius(8px 8px 0 0); }
    &::before {
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $base-color-two;
      opacity: 0.65;
      z-index: -1; } }
  .right {
    width: 50%;
    padding: rem(40px) rem(30px);
    border: 2px solid $border-color;
    border-left: none;
    @include border-radius(0 8px 8px 0);
    @include media(991px) {
      width: 100%;
      @include border-radius(0 0 8px 8px);
      border: 2px solid $border-color;
      border-top: none; }
    .title {
      font-weight: 700;
      font-size: rem(28px);
      position: relative;
      padding-bottom: 5px;
      margin-bottom: rem(30px);
      &::after {
        position: absolute;
        content: '';
        bottom: 0;
        left: 0;
        width: 30px;
        height: 2px;
        background-color: $base-color; } } } }
/* account section css end */

/* contact section css start */
.contact-form-wrapper {
  padding: rem(50px);
  border: 2px solid $border-color;
  @include border-radius(5px);
  @include media(1399px) {
    padding: rem(50px) rem(30px); }
  .title {
    margin-bottom: rem(30px);
    font-weight: 600; } }
.contact-info-card {
  @include d-flex;
  align-items: center;
  padding: rem(20px);
  border: 1px solid $border-color;
  box-shadow: 0 5px 10px rgba(#000, 0.05);
  @include border-radius(5px);
  height: 100%;
  &__icon {
    width: 75px;
    height: 75px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: $base-color;
    @include border-radius(5px);
    i {
      font-size: rem(34px);
      color: #fff; } }
  &__content {
    width: calc(100% - 75px);
    padding-left: rem(20px);
    .title {
      margin-bottom: rem(5px); }
    a {
      color: $para-color;
      &:hover {
        color: $base-color; } } } }
.map-wrapper {
  padding: 15px;
  background-color: #fff;
  box-shadow: 0 0 15px rgba(#000, 0.05);
  @include border-radius(5px); }
/* contact section css end */
